/* <---FLEX---> */
.flex{
    display: flex;
}

.flex.wrap{
  flex-wrap: wrap;
}

.flex.center{
  align-items: center;
}
.flex.j-center{
  justify-content: center;
}
.flex.s-b{
justify-content: space-between;
}
.flex.col{
  flex-direction: column; 
}
.flex.col-st{
  flex-direction: column;
  align-items: flex-start;
}