.app-container {
    display: grid;
    grid-template-columns: 1fr calc(100% - 200px) 1fr;
}

header.main-header {
    grid-column: 1 / -1;
}

.main-app-container {
    position: relative;
    grid-column: 2;
    min-height: calc(100vh - 200px);
}

.main-side-bar {
    grid-column: 1;
}

footer.main-footer {
    grid-column: 1 / -1;
}