div.filter-form{
    margin-top: 30px;
    margin-left: 10px;
    input {
        border-radius: 10px;
        border: 1px solid $clr3;
        padding: 5px 10px;
        margin-top: 10px;
        &[type="number"]{
            margin-right: 10px;
            width: 22%;
        }
    }
    select{
        border-radius: 10px;
        border: 1px solid $clr3;
        padding: 5px 10px;
        width: 148%;
        color: $clr3;
    }
    button {
        position: relative;
        left: 8em;
        background: transparent;
        background-color: $clr1;
        color: white;
        border: 1px solid $clr1;
        border-radius: 2em;
        padding: 5px 10px;
        margin-top: 10px;
    }
}